export default {
	data() {
		return {
			timer:["09:00-10:00","09:00-10:00",""],
			calendar:[]
		}
	},
	mounted() {
		this.getCurrentDate();
	},
	methods:{
		changeItem(i){
			let item = this.calendar[i];
			this.calendar.forEach(item=>{
				item.select = false
			})
			item.select = true;
		},
		// 计算日期
		getCurrentDate(format){
			var now = new Date();
			var month = now.getMonth();
			var date = now.getDate();
			var day = now.getDay();
			month = month + 1;
			let week = "1234560";
			let result = [];
			
			let arr = week.split(day)
			
			console.log(week,day,arr)
			let arr1 = arr[0].split("").reverse().forEach(item=>{
				let oTime =  this.getDay(-item)
				result.push(oTime);
			})
			result.push(
				{
					week:'星期'+ this.returnWeek(day),
					day:date,
					month:month+'月',
					select:true
				}
			);
			if(day!=0){
				let timer = 7-day;
				for(let i = 1; i<=timer; i++){
					let oTime =  this.getDay(+i)
					result.push(oTime);
				}
			}
			
			this.calendar = result;
		},
		getDay(num) {
			var today = new Date();
			var nowTime = today.getTime();
		    var ms = 24*3600*1000*num;
		    today.setTime(parseInt(nowTime + ms));
		    var oYear = today.getFullYear();
		    var oMoth = (today.getMonth() + 1).toString();
		    var oDate = today.getDate().toString();
			var oday = today.getDay();
			
			return {
					week:'星期'+ this.returnWeek(oday),
					day:oDate,
					month:oMoth+'月',
					select:false
				}
		},
		returnWeek(i){
			let week = '';
			if(i == 0 || i == 7){
				week = "日"
			}else if(i == 1){
				week = "一"
			}else if(i == 2){
				week = "二"
			}else if(i == 3){
				week = "三"
			}else if(i == 4){
				week = "四"
			}else if(i == 5){
				week = "五"
			}else if(i == 6){
				week = "六"
			}
			return week;
		}
	}
}
